*{
  font-family: helvetica, arial, sans-serif;
}

body {
  padding: 60px;

}
input{
  width: 100%;
  font-size:20px;
}
#root{
  width: 70%;
  height: 100%;
  margin: 0 auto;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card{
  align-content: center;
  border: 1px solid  grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  width: 250px;
  height: 300px;
  overflow: hidden;
}

.card p{
  padding: 5px;
  text-align: center;
}
.card img{
  width: 100%;
  height: 75%;
  
}